html[dir="rtl"]
.welaunch-container {
    .welaunch-action_bar {
        float: left;
    }
}

.welaunch-container .expand_options,
.welaunch-sidebar,
.cb-enable,
.cb-disable,
.cb-enable span,
.cb-disable span,
#welaunch-footer #welaunch-share {
    float: right;
}

.welaunch-main {
    border-left: 0px;
    margin-left: 0px;
    border-right: 1px solid #d8d8d8;
    margin-right: 201px;

    .welaunch-typography-container .typography-preview {
        text-align: center !important;
        direction: ltr !important;
    }
}

.welaunch-group-tab-link-a {
    padding-left: 0px;
    padding-right: 30px;

    i {
        padding-left: 10px;
        padding-right: 5px;
    }

    span.group_title {
        padding-left: 0px;
        padding-right: 30px;
    }
}

.welaunch_slider {
    margin-left: 0px;
    margin-right: 15px;
}

.welaunch-action_bar {
    float: left !important;
}

.expand_options {
    float: right !important;
    border: 1px solid #f00;
}

.welaunch_field_th {
    padding: 20px 0 20px 10px !important;
}

.field-desc {
    text-align: right;
}

.welaunch-container-ace_editor,
.welaunch-container-border,
.welaunch-container-spacing,
.welaunch-container-dimensions {
    direction: ltr !important;
}

.welaunch-container-border .field-border-input,
.welaunch-container-border .welaunch-color-init,
.welaunch-container-border .welaunch-border-style,
.welaunch-container-sorter,
.welaunch-container-border,
.welaunch-container-spacing,
.welaunch-container-spacing .field-spacing-input,
.welaunch-container-dimensions .welaunch-dimensions-container,
.welaunch-container-text label,
.welaunch-container-checkbox input,
.typography-font-bar,
.typography-style-bar,
.welaunch-color.welaunch-typography-color,
.welaunch-typography-subsets {
    float: right !important;
}

.input-append {
    margin-right: 10px;
    direction: ltr !important;
}

.welaunch-container-slider,
.welaunch-container-spinner,
.welaunch-container-switch {
    direction: ltr !important;
    float: right;
    margin: 0;
}

.welaunch-info-field {
    .welaunch-info-icon {
        margin-left: 15px;
    }
}

#welaunch-share {
    float: right !important;
}

.welaunch-sidebar {
    .welaunch-menu-warning,
    .welaunch-menu-error,
    .hasSubSections .extraIconSubsections {
        float: left;
    }
}

.select2-search-choice-close {
    left: 23px !important;
}
